@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.poppins{font-family:'Poppins', sans-serif}*{font-family:'Poppins', sans-serif}body{background:#212529}.temp-disclaimer{display:none;padding:.5rem}.temp-disclaimer.dark{background:#212529}.temp-disclaimer.light{background:#f8f9fa}@media (max-width: 991.98px){.temp-disclaimer{display:block}}

.hero-banner{width:100%;min-height:40rem;box-sizing:border-box;display:flex}.hero-banner .right-container,.hero-banner .left-container{flex:1 1;min-width:50%;margin:auto}.hero-banner .left-container{display:none}@media (min-width: 992px){.hero-banner .left-container{display:block}}.hero-banner.light{background:linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0) 40%, #f8f9fa 90%, #f8f9fa 90%),url(/static/media/bg_trianglify_light_sm.4308cf2c.png) center no-repeat;background-size:cover}@media (min-width: 768px){.hero-banner.light{height:100vh;background:linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0) 40%, #f8f9fa 90%, #f8f9fa 90%),url(/static/media/bg_trianglify_light_md.a915a4b2.png) center no-repeat;background-size:cover}}@media (min-width: 992px){.hero-banner.light{background:linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0) 40%, #f8f9fa 90%, #f8f9fa 90%),url(/static/media/bg_trianglify_light_lg.518f8618.png) center no-repeat;background-size:cover}}.hero-banner.dark{background:linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0) 40%, #212529 90%, #212529 90%),url(/static/media/bg_trianglify_dark_sm.1b4a6809.png) center no-repeat;background-size:cover}@media (min-width: 768px){.hero-banner.dark{height:100vh;background:linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0) 40%, #212529 90%, #212529 90%),url(/static/media/bg_trianglify_dark_md.56e26bbe.png) center no-repeat;background-size:cover}}@media (min-width: 992px){.hero-banner.dark{background:linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0) 40%, #212529 90%, #212529 90%),url(/static/media/bg_trianglify_dark_lg.772a7199.png) center no-repeat;background-size:cover}}

.disclaimer{border-radius:1rem;margin:auto}@media (min-width: 768px){.disclaimer{width:75%}}@media (min-width: 1400px){.disclaimer{width:50%}}.disclaimer-header{padding:.7rem 2rem}.disclaimer-body{padding:1rem 2rem}.disclaimer.primary{border-left:0.5rem solid #20A4F3}.disclaimer.secondary{border-left:0.5rem solid #6c757d}.disclaimer.success{border-left:0.5rem solid #198754}.disclaimer.info{border-left:0.5rem solid #0dcaf0}.disclaimer.warning{border-left:0.5rem solid #ffc107}.disclaimer.danger{border-left:0.5rem solid #dc3545}.disclaimer.light{border-left:0.5rem solid #f8f9fa}.disclaimer.dark{border-left:0.5rem solid #212529}.disclaimer.light{background:#f8f9fa;color:#212529;box-shadow:4px 10px 3px rgba(0,0,0,0.4)}.disclaimer.dark{background:#212529;color:#f8f9fa;box-shadow:4px 10px 3px rgba(0,0,0,0.2);border:1px solid #16181b}

.milestone{margin:auto}.milestone.scrollable{max-height:500px;overflow-y:scroll}.milestone.scrollable::-webkit-scrollbar{background-color:transparent;border-radius:1rem;width:.8rem}.milestone.scrollable::-webkit-scrollbar-thumb:window-inactive,.milestone.scrollable::-webkit-scrollbar-thumb{border-radius:1rem;background:#adb5bd}.milestone ul{list-style-type:none;counter-reset:step;cursor:pointer}.scrollable.milestone ul{margin-right:.5rem}@media (max-width: 767.98px){.milestone ul{-webkit-padding-start:unset;padding-inline-start:unset}}.milestone ul li{border-radius:1rem;padding:1.5rem 1rem 1.5rem 5rem;margin-bottom:.5rem;position:relative;transition:.3s transform ease}@media (max-width: 767.98px){.milestone ul li{padding:1.5rem 1rem 1.5rem 3.5rem}}.milestone ul li:hover{transform:translateY(-5px)}.milestone ul li:before{counter-increment:step;content:counter(step);position:absolute;left:1rem;top:1.1rem;width:1rem;height:1rem;padding:.5rem;font-size:.8rem;text-align:center;border-radius:50%}.milestone.light ul li{box-shadow:1px 2px 1px rgba(0,0,0,0.1);border:1px solid #e9ecef;background:#f8f9fa;color:#212529}.milestone.light ul li:hover{box-shadow:2px 8px 6px rgba(0,0,0,0.1)}.milestone.light ul li:before{background:#212529;color:#fff}.milestone.dark ul li{box-shadow:1px 2px 1px rgba(0,0,0,0.3);border:1px solid #16181b;background:#212529;color:#f8f9fa}.milestone.dark ul li:hover{box-shadow:2px 8px 6px rgba(0,0,0,0.3)}.milestone.dark ul li:before{background:#495057;color:#212529}

.person-card{text-align:center;padding:.5rem}@media (min-width: 768px){.person-card h1{font-size:5rem}}@media (min-width: 992px){.dark.person-card h1{text-shadow:8px 6px 2px rgba(0,0,0,0.3)}}.person-card .description-content{margin:auto}@media (min-width: 768px){.person-card .description-content{font-size:1.8rem}}.person-card .description-content p{margin:0 auto}.dark.person-card .description-content p{text-shadow:3px 2px 2px rgba(0,0,0,0.5)}.person-card .description-content .lang-list{margin:5rem 0}.person-card .description-content .lang-list ul{padding-left:0;list-style-type:none}.person-card .description-content .lang-list ul li{font-size:3rem;padding:0 2rem;display:inline;vertical-align:middle}.person-card .description-content .lang-list ul li a{text-decoration:none}.light.person-card .description-content .lang-list ul li a{color:#212529}.dark.person-card .description-content .lang-list ul li a{color:#f8f9fa}.person-card.dark{text-shadow:4px 2px 4px rgba(0,0,0,0.3);color:#fff}.person-card.light{color:#212529}

.custom-navbar{z-index:90;position:fixed;top:0;left:0;width:100%;background:transparent;transition:background-color .2s linear}.custom-navbar.opaque{background:#212529}.custom-navbar.opaque.light{background:#f8f9fa}.custom-navbar.opaque.dark{background:#212529}.custom-navbar .main-container{display:flex;height:100%;flex-direction:row;flex-wrap:wrap;align-items:center;justify-content:space-between;padding:1rem}.custom-navbar .main-container .right-container,.custom-navbar .main-container .left-container{flex:1 1 50px;max-width:33%}.custom-navbar .main-container .right-container{text-align:right}.custom-navbar .main-container .left-container{text-align:left}

.toggle input[type=checkbox]{display:none}

.light.toggle label{color:#212529}.dark.toggle label{color:#f8f9fa}

